import { useSelector } from "react-redux";
import { ThemeProvider } from "styled-components";
import { GlobalStyle } from "./globalStyle";

// ***        ***
//       
// color:${({theme})=>theme.variableColors.primaryBackground}

const lightTheme = {
  variableColors: {
    sideBar: "#FFFFFF", //   
    primaryBackground: "#F7F6F9", //    
    secondaryBackground: "#FFFFFF", //  
    buttonBlue: "#E3F3FF", //  
    text: "#111111", //  
  },
};

const darkTheme = {
  variableColors: {
    sideBar: "#13151A", //   
    primaryBackground: "#171820", //    
    secondaryBackground: "#21222C", //  
    buttonBlue: "#3E85F3", //  
    text: "#FFFFFF", //  
  },
};

// ***    ***  
//       
// color:${({theme})=>theme.testcolor1}

const otherColors = {
  testcolor1: "#ff0000",
  testcolor2: "#ff00ff",
};

 
export const Theme = ({ children }) => {
  const currentTheme = useSelector((state) => state.theme.currentTheme);
  const theme = (checkTheme=true) => {
    return checkTheme
      ? { ...lightTheme, ...otherColors }
      : { ...darkTheme, ...otherColors };
  };
  console.log(currentTheme)
  console.log(theme(currentTheme));


  return (
    <ThemeProvider theme={theme(currentTheme)}>
      <GlobalStyle />
      {children}
    </ThemeProvider>
  );
};
